html {
    font-size: 16px;
}

.Playlist {
    position: relative;
    display: inline-block;
    width: 500px;
    height: 750px;
    flex-shrink: 0;
    border-radius: 20px;
    border: 1px solid #FFF;
    background: linear-gradient(142deg, rgba(255, 255, 255, 0.80) 0%, rgba(255, 255, 255, 0.30) 100%);
    box-shadow: 0px 4px 40px -1px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(20px);
    overflow-y: scroll;
    margin-bottom: 2rem;
    align-items: center;
}

.Playlist input {
    display: block;
    box-sizing: border-box;
}

.Playlist-name {
    border-bottom: 1px solid #133C7C;
    margin: 1rem 2rem 1rem 2rem;
}

.Playlist input {
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    color: #133C7C;
    font-family: Poppins;
    font-size: 25px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-align: left;
}
  
.Playlist-save {
    width: 135px;
    height: 45px;
    flex-shrink: 0;
    border-radius: 70px;
    background: #133C7C;
    outline: none;
    border: none;
    color: white;
    text-align: center;
    font-family: Poppins;
    font-size: 0.87rem;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
  
.Playlist-save:hover {
    background-color: #0C2853;
}
  

input::placeholder {
    color: #A9A9A9;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 700px) {
    .Playlist {
        width: 100%;
    }
}